import { Modal } from "@chac/common/components/Modal";
import styled from "styled-components";

import React, {useState, useEffect} from "react";
/**
 * Dimmed 등 기본적인 modal css를 위해 <Modal type="custom" > 로 감싸준다
 */

const StyledContainer = styled.div`
	div.input-box {
		text-align: left;
		padding: 15px;
		border: solid 1px #e2e2e2;
		border-radius: 10px;
		margin: 10px 0;
		border-color: '#e2e2e2';
		background-color: '#ffffff';
		input {
			font-size: 1.2rem;
			font-weight: 400;
			width: auto;
		}
	}
	ul {
		max-height: 200px;
	}
`;
const PetListItem = ({petName, onClick}) => (<li onClick={onClick}>{petName}</li>)
export default function SearchPetKindPopup({data, close}) {
	const [keyword, setKeyword] = useState('');
	const [displayData, setDisplayData] = useState(data.props.petInfos);
	useEffect(() => {
		const debounce = setTimeout(() => {
			console.log(keyword);
			if (keyword) {
				setDisplayData(data.props.petInfos.filter(item => item.petNm.includes(keyword)))
			}
		}, 200)
		return () => {
			clearTimeout(debounce)
		}
	},[keyword])

	// close handler
	const closeHandler = () => {
		close('견종입력값 전달')
	}
	
	console.log(displayData)
	// seq: 207, petCd: 'C0056', petNm: '고든 세터'
	return (
		<Modal type="bottomSheet" close={close}>
			<StyledContainer>
				<h2>펫 검색 팝업</h2>
				<div className="input-box">
					<input type="text" value={keyword} onChange={(e) => {setKeyword(e.target.value)}}></input>
				</div>
				{/* 펫 목록 */}
				<ul>
					{
						displayData.map((value, index) => <PetListItem key={index} petName={value.petNm} onClick={(e) => close(value)} />)
					}
				</ul>

				<div>
					<button onClick={closeHandler}>hello</button>
				</div>

			</StyledContainer>
		</Modal>
	);
}
